.main {
    margin-left: 48px;
    margin-right: 25px;
    mix-width: 200px;
    width: 1400px;
    margin-top: 50px;
}

.main_title {
    font-size: 25px;
    font-weight: 700;
    display: flex;
    line-height: 1.2;
    text-transform: uppercase;
    margin-left: 32px;
}

.title_anotherColor {
    margin-right: 5px;
    color: #ce7852;
}

.items_wrapper {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
    flex-wrap: wrap;
}

.item {
    justify-content: inherit;
    display: flex;
    align-items: center;
    margin-top: 15px;
    height: 90px;
    width: 297px;
    background-color: #ffffff;
    border: 5px solid #f8f3ff;
}

.title {
    margin-left: 5px;
    font-size: 20px;
    font-weight: 700;
    line-height: 1.2;
    color: #3c3c3c;
}

.photo {
    margin-right: 15px;
    height: 42px;
    width: 48px;
}

hr {
    background-color: #f8f3ff;
    margin-top: 45px;
    border: none;
    color: #333;
    overflow: visible;
    text-align: center;
    height: 5px;
}

hr:after {
    background-color: #fff;
    content: url('./items/009.png');
    width: 15px;
    height: 15px;
    position: relative;
    top: -20px;
}

.item:hover {
    border: 3px solid saddlebrown;
}